___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --color-primary: #353535;
  --bg-color: rgb(243, 242, 242);
  --text-color: #000000;
  --btn-primary-bg: #007bff;
  --btn-primary-color: #ffffff;
  --btn-dark-bg: #343a40;
  --btn-dark-color: #ffffff;
  --btn-light-bg: #f8f9fa;
  --btn-light-color: #212529;
}

/* Preloader styles */
.preloader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: white;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
  transition: opacity 0.3s ease-out;
}

.preloader.fade-out {
  opacity: 0;
}

.preloader img {
  width: 80px;
  height: 80px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

body {
  background-color: var(--bg-color);
  color: var(--text-color);
  transition:
    background-color 0.3s ease,
    color 0.3s ease;
}

body.dark-mode {
  --bg-color: #393838;
  --text-color: #ffffff;
  --color-primary: #007bff;
  --btn-primary-bg: #0d1117;
  --btn-primary-color: #ffffff;
  --btn-dark-bg: #4a4646;
  --btn-dark-color: #ddd;
  --btn-light-bg: #2a2a2a;
  --btn-light-color: #e1e1e1;
}

/* navbar */
.navbar-nav {
  position: relative;
  flex-direction: row !important;
}

p {
  margin: 0;
}
.blur {
  backdrop-filter: blur(10px);
  background-color: rgba(255, 255, 255, 0.7);
  transition: background-color 0.3s ease;
}
.blur2 {
  backdrop-filter: blur(10px);
  background-color: transparent;
  transition: background-color 0.3s ease;
}

body.dark-mode .blur {
  background-color: rgba(30, 30, 30, 0.7);
}

body.dark-mode a {
  color: var(--btn-primary-color) !important;
}

body.dark-mode .bg-light {
  background: #262626 !important;
}

body.dark-mode .nav-pills .nav-link.active {
  background: #555b63 !important;
}
.navbar-nav .active {
  position: relative;
  font-weight: 600;
  /* text-decoration: underline; */
  color: #4caf50 !important;
}

.nav-link {
  cursor: pointer;
}

.nav-link:hover {
  text-decoration: underline;
}

.nav-pills .nav-link.active,
.nav-pills .show > .nav-link {
  color: white !important;
  background-color: var(--bs-nav-pills-link-active-bg);
}

.navbar-nav .active:hover {
  color: rgb(57, 57, 57) !important;
  text-decoration: underline;
}

/* Buttons */
.btn-primary {
  background-color: var(--btn-primary-bg);
  color: var(--btn-primary-color);
  border-color: var(--btn-primary-bg);
  transition:
    background-color 0.3s ease,
    color 0.3s ease;
}

.btn-primary:hover {
  background-color: #2a4a6f;
  border-color: #2a4a6f;
}

.btn-light {
  background-color: var(--btn-light-bg);
  color: var(--btn-light-color);
  border-color: var(--btn-light-bg);
  transition:
    background-color 0.3s ease,
    color 0.3s ease;
}

.btn-light:hover {
  background-color: #e2e6ea;
  border-color: #e2e6ea;
}

body.dark-mode .btn-primary:hover {
  background-color: #274060;
  border-color: #274060;
}

.btn-dark {
  background-color: var(--btn-dark-bg);
  color: var(--btn-dark-color);
  border-color: var(--btn-dark-bg);
  transition:
    background-color 0.3s ease,
    color 0.3s ease;
}

.btn-dark:hover {
  background-color: #333333;
  border-color: #333333;
}

body.dark-mode .btn-dark:hover {
  background-color: #111111;
  border-color: #111111;
}

body.dark-mode .btn-light {
  background-color: var(--btn-light-bg);
  color: var(--btn-light-color);
  border-color: var(--btn-light-bg);
}

body.dark-mode .btn-light:hover {
  background-color: #1f1f1f;
  border-color: #1f1f1f;
}

body.dark-mode .text-muted,
body.dark-mode .form-text {
  color: var(--btn-dark-color) !important;
}

body.dark-mode .project {
  box-shadow: 0 0 10px rgb(255 255 255 / 24%);
}
body.dark-mode .bg-body-tertiary {
  background: #2a2a2a !important;
}
body.dark-mode .navbar-toggler {
  background-color: white !important;
}

.header-grid {
  display: grid;
  grid-template-columns: 2fr 1fr;
}
.location-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.location-name {
  justify-self: end;
  align-self: center;
}
.content-grid {
  display: grid;
  grid-template-columns: 1fr 1fr 250px;
}
.date-time {
  align-self: anchor-center;
}
.weekdays {
  display: grid;
  border: 1px solid;
  grid-template-columns: 1fr;
  grid-template-rows: 50px;
  border-radius: 20px;
  gap: 5px;
  box-shadow: 2px 0px 4px rgba(30, 30, 30, 0.7);
}
.weekdays-inner {
  display: grid;
  grid-template-columns: 50px 1fr 50px;
  margin-top: 10px;
  cursor: pointer;
}
.active {
  border-left: 3px solid rgb(2, 173, 59);
}
.pb-100 {
  padding-bottom: 100px;
}
.pt-100 {
  padding-top: 100px;
}
.py-100 {
  padding: 100px 0 100px 0;
}
.mt-100 {
  margin: 100px;
}
.top-8 {
  top: 8%;
}
.self-baseline {
  align-self: baseline;
}
.self-center {
  justify-self: center;
}
.align-grid-center {
  align-self: center;
}
.input-group {
  width: max-content !important;
}
.lead {
  font-size: 0.8rem;
}
.form-control {
  box-shadow: none !important;
}
.fs-07 {
  font-size: 1rem;
  top: -40px;
}
.fs-08 {
  font-size: 0.6em;
  top: -26px;
}

.shape {
  overflow: hidden;
  position: relative;
}
.shape::before {
  content: "";
  position: absolute;
  bottom: -1px;
  left: -1px;
  right: -1px;
  top: -1px;
  z-index: 3;
  pointer-events: none;
  background-repeat: no-repeat;
  background-size: 100% 90px;
  background-position: 50% 0%;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

@media (min-width: 768px) {
  .shape::before {
    background-size: 100% 90px;
    background-position: 50% 0%;
  }
}

@media (min-width: 1025px) {
  .shape::before {
    bottom: -0.1vw;
    left: -0.1vw;
    right: -0.1vw;
    top: -0.1vw;
    background-size: 100% 182px;
    background-position: 50% 100%;
    transform: rotateY(180deg);
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  }
}
@media (min-width: 2100px) {
  .shape::before {
    background-size: 100% calc(2vw + 182px);
  }
}
@media (max-width: 890px) {
  .content-grid {
    grid-template-columns: 1fr;
    padding-bottom: 100px !important;
  }
  .content-grid > div:nth-of-type(2),
  .shape {
    display: none !important;
  }
  .header-grid {
    margin-top: 10px;
  }
  .description {
    width: 22ch;
  }
  .location-grid {
    justify-self: baseline;
  }
}
